<template>
  <div class="updpassword">
    <input type="password" name="opassword" v-model="opassword" placeholder="请输入旧密码" @change="opasswordChange"/>
    <div class="res" >
      <div v-show="isopassword">请输入6位以上的密码</div> 
      </div>
    <input type="password" name="npassword2" v-model="npassword1" placeholder="请输入新密码" @change="npassword1Change"/>
    <div class="res" >
      <div v-show="isnpassword1">请输入6位以上的密码</div>
      </div>
    <input type="password" name="npassword2" v-model="npassword2" placeholder="请再次输入新密码" @change="npassword2Change"/>
    <div class="res" >
      <div v-show="isnpassword2">两次输入的密码不一致</div>
      </div>
    <input type="button" value="修改密码" class="u-button" @click="updPassword">
  </div>
</template>

<script>
  import {updUpassword} from 'network/user'
  export default {
    name: 'UpdatePassword',
    data () {
      return {
        opassword: '',
        isopassword: false,
        npassword1: '',
        isnpassword1: false,
        npassword2: '',
        isnpassword2: false
      }
    },
    computed: {
      //获取用户信息
      getUserInfo() {
        return this.$store.state.userInfo
      }
    },
    methods: {
      opasswordChange() {
        if(this.opassword.length < 6) {
          this.isopassword = true
        }else{
          this.isopassword = false
        }
      },
      npassword1Change() {
        if(this.npassword1.length < 6) {
          this.isnpassword1 = true
        } else {
          this.isnpassword1 = false
        }
      },
      npassword2Change() {
        if(this.npassword2 != this.npassword1) {
          this.isnpassword2 = true
        } else {
          this.isnpassword2 = false
        }
      },
      updPassword() {
        //修改密码
        if(this.opassword != '' && this.npassword1 != '' && this.npassword2 != '' && !this.isopassword && !this.isnpassword1 && !this.isnpassword2){
          updUpassword(this.getUserInfo.uid,this.npassword1,this.opassword).then(res => {
              this.$toast.show(res)
              this.$router.push('/profile')
          })
        } else {
          this.$toast.show("请填入正确的信息")
        }
      }
    }
  }
</script>

<style scoped>
  .updpassword{
    margin: auto;
    margin-top: 30px;
    text-align: center;
  }
  input {
    width: 200px;
    border-radius: 5px;
    display: block;
    margin: auto;
  }
  .res{
    height: 30px;
    color: red;
  }
  .u-button{
    background-color: forestgreen;
    height: 30px;
    width: 80px;
    margin: auto;
    font-size: 16px;
    line-height: 25px;
    border-radius: 30px;
  }
</style>